    },
    removeFromMyCandidates (index) {
      this.candidateList.splice(index, 1);
    },
    buildURL () {
      let base = "https://api.open.fec.gov/v1/candidates/totals/?sort_null_only=false&q=";
      let more = "&sort_nulls_last=false&per_page=20&sort_hide_null=false&election_full=true&api_key=";
      let key = "W4QuBM5KifGf5EYxYd4CDuXYKmn97mOY58cGdSPN";
      let extra = "&page=1";
      let myArray = [base, this.searchName, more, key, extra];
      let myURL = myArray.join("");
      this.URL = myURL;
    },
    buildCommitteeSearchURL (candidateID){
      let base = "https://api.open.fec.gov/v1/committees/?candidate_id=";
      let more = "&sort_null_only=false&sort=name&sort_nulls_last=false&per_page=20&sort_hide_null=false&api_key="
      let key = "W4QuBM5KifGf5EYxYd4CDuXYKmn97mOY58cGdSPN";
      let extra = "&page=1"
      let myArray = [base, candidateID, more, key, extra];
      let myURL = myArray.join("");
      //console.log("committee search url built:");
      return (myURL);
    },
    buildCommitteeHistoryURL (committeeID){
      let base = "https://api.open.fec.gov/v1/committee/";
      let more = "/reports/?per_page=100&api_key="
      let key = "W4QuBM5KifGf5EYxYd4CDuXYKmn97mOY58cGdSPN";

      let myArray = [base, committeeID, more, key]
      let myURL = myArray.join("");

      //console.log("committee history url built:");
      //console.log(myURL);
      return (myURL);
    },
    downloadData (){
      var wb = XLSX.utils.book_new();
      
      for(var i = 0; i < this.JSONHist.length; i++){
        var ws = XLSX.utils.json_to_sheet(this.JSONHist[i])
        console.log("WORKSHEET:");
        console.log(ws);
        XLSX.utils.book_append_sheet(wb, ws, this.JSONHist[i][0].committee_name.substring(0,30));
        console.log("WORKSHEET APPENDED");
      }
      XLSX.writeFile(wb, 'out.xlsb');
    },







    async getData () {
      console.log("Get data funciton called");
      this.buildURL();

      const response = await fetch (this.URL).then(response => response.json());
      console.log(response);

      this.JSONResponse = response;

    },
    async runSearch () {

      this.candidates = [];

      // BUILD CANDIDATES DATABASE WITH CANDIDATELIST INFO
      for (var l = 0; l < this.candidateList.length; l ++){

        let candidate = {"id":this.candidateList[l].candidate_id, "candSearch":this.candidateList[l], "commSearch":[]};
        this.candidates.push(candidate);

      }


      for(var i = 0; i < this.candidateList.length; i++) {
        let candidateID = this.candidateList[i].candidate_id;
        let comURL = this.buildCommitteeSearchURL(candidateID);

        const commSearchResponse = await fetch (comURL).then(response => response.json());


        for(var j = 0; j < commSearchResponse.results.length; j++){

          if(commSearchResponse.results[j].designation == "P"){

            //console.log("Found committee ID: " + commSearchResponse.results[j].committee_id);
            this.findCommitteeHistory(commSearchResponse.results[j].committee_id, i);
          
          }
          
        }

      }



    },
    async findCommitteeHistory (committeeID, index) {
      console.log("find committee history function run");

      let commHistURL = this.buildCommitteeHistoryURL(committeeID);
      const commHistSearchResponse = await fetch (commHistURL).then(response => response.json());

      var results = commHistSearchResponse.results;

      this.JSONHist.push(results);


    },
    
